export default {
	methods: {
		moveUp (i) {
			if (i == 0) return;
			this.$store.commit('MOVEUP', i)
		},
		moveDown (i) {
			if (i == this.list.length - 1) return;
			this.$store.commit('MOVEDOWN', i)
		}
	},
	computed: {
		list () {
			return this.$store.state.list
		}
	}
}
